import React from 'react';
import './Skills.css'
import './Header.css'
export const Skills=()=>{
    let skillCount=1;
    return(<div>
        <h2 className='blue'>Skills Component</h2>
        <h3 className='red'>Red class of header</h3>
        <button onClick={()=>{
            skillCount++;
            console.log("skill count is "+skillCount);
        }}>Add Skills</button>
        <p>Total skills are {skillCount}</p>
    </div>)
}